import { FC, useEffect, useState } from "react";
import Head from "next/head";
import { useRouter } from "next/router";
import { nanoid } from "nanoid";
import { sha256 } from "js-sha256";
import { HIDE_WHATSAPP_WIDGET } from "constants/enum";

import { FixedButtonContextProvider } from "common/context/fixedButtonContext";
import {
    FacebookEvents,
    sendFacebookEvent,
    sendSnapchatPixelEvent,
    SnapchatPixelEvents,
} from "common/utils/metrics";
import { postFacebookEvent } from "common/api/facebook";
import { getCookiesValueByName } from "common/utils/cookie";
import { COOKIES } from "common/constants/enum";
import {
    SourceProps,
    WhatsappWidget,
} from "common/components/new/WhatsappWidget";

import { BaseLayoutProps } from "./BaseLayout.types";

const BaseLayout: FC<BaseLayoutProps> = (props) => {
    const { title, content, children, description } = props;
    const [isFixedButton, setFixedButton] = useState(true);

    const hasWindow = typeof window !== "undefined";
    const router = useRouter();

    const hideWhatsAppWidget = HIDE_WHATSAPP_WIDGET.includes(router?.pathname);

    useEffect(() => {
        if (!hasWindow) {
            return;
        }

        const email = decodeURIComponent(getCookiesValueByName(COOKIES.EMAIL));
        const phone = decodeURIComponent(getCookiesValueByName(COOKIES.PHONE));

        sendSnapchatPixelEvent(
            SnapchatPixelEvents.SNAPCHAT_PIXEL_PAGE_VIEW_EVENT,
            {
                ...(email && {
                    user_email: email,
                    user_hashed_email: sha256(email),
                }),
                ...(phone && {
                    user_phone_number: phone,
                    user_hashed_phone_number: sha256(phone),
                }),
            }
        );

        // Events fire
        const eventID = nanoid();

        sendFacebookEvent(
            FacebookEvents.FORM_SUBMIT_FACEBOOK_PAGE_VIEW_EVENT,
            {},
            {
                eventID,
            }
        );

        const sendConversionApiEvent = async () => {
            await postFacebookEvent({
                eventName: FacebookEvents.FORM_SUBMIT_FACEBOOK_PAGE_VIEW_EVENT,
                eventSourceUrl: location.href,
                eventId: eventID,
                userData: {
                    // client_user_agent: window.navigator.userAgent,
                    fbp: decodeURIComponent(getCookiesValueByName(COOKIES.FBP)),
                    fbc: decodeURIComponent(getCookiesValueByName(COOKIES.FBC)),
                },
            });
        };

        sendConversionApiEvent();
    }, [hasWindow]);

    //@ts-ignore
    const { version } = props.children?.props;

    return (
        <div>
            <FixedButtonContextProvider
                value={{ isFixedButton, setFixedButton }}
            >
                <Head>
                    <meta name="description" content={description} />
                    <title>{title}</title>
                </Head>
                {children}
                {content.whatsApp && !hideWhatsAppWidget && (
                    <WhatsappWidget
                        {...content.whatsApp}
                        hide={!isFixedButton}
                        source={SourceProps.FROM_LANDING}
                        version={version}
                    />
                )}
            </FixedButtonContextProvider>
        </div>
    );
};

export default BaseLayout;
